body {
    overflow: hidden;
}

.live-main {
    height: 100vh;
    width: 100vw;
    background-color: #900c3e;
    background-size: cover;
    background-repeat: no-repeat
}

.main-area {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 50px;
    grid-template-rows: repeat(10, 1fr);
    height: 90vh;
    padding: 1rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.modal-open .modal {
    overflow-y: hidden;
}
.modal.show .modal-dialog {
    height: 90vh;
    overflow-y: auto
}
.modal-dialog::-webkit-scrollbar {
    width: 8px;
    height: 5px;
}
.modal-dialog::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #ff8a00, #da1b60);
    border-radius: 10px;
    box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .25), inset -2px -2px 2px rgba(0, 0, 0, .25);
}
.modal-dialog::-webkit-scrollbar-track {
    background: linear-gradient(to right, #201c29, #201c29 1px, #100e17 1px, #100e17);
}
.live-controls {
    position: absolute;
    left: 60%;
    z-index: 999;
    bottom: 18%;
}
.input-group-append .btn-load {
    border-top-right-radius: .25rem !important;
    border-bottom-right-radius: .25rem !important;
}
.errorAlert {
    z-index: 999999;
    width: 600px;
    right: 15px;
    position: absolute;
    top: 15px;
    opacity: .9;
}

.btn-control {
    background-image: url('../assets/images/logo.png');
    background-repeat: no-repeat;
    background-color: transparent !important;
    border: 0 !important;
    opacity: 0.5;
    background-size: contain;
    width: 100px;
    height: 50px;
    box-shadow: none !important;
}

.btn-control:hover {
    cursor: pointer;
}

.close {
    color: #fff;
}

.close:hover {
    color: #c9c9c9;
}

.modal-content {
    background-color: #100e17;
    color: #fff;
}

.modal-header {
    border-bottom: 1px solid #17141d;
}
.modal-footer {
    border-top: 1px solid #17141d;
    color: #7a7a8c;
}
.modal-footer a {
    color: #7a7a8c;
}
.form-control:focus {
    border-color: transparent;
    box-shadow: none;
    color: #fff;
    background-color: #15151f;
}

.form-control::placeholder {
    color: #7a7a8c;
}

.input-group-text {
    background-color: transparent;
    border: 0;
    color: #fff;
}

.form-control {
    color: #ffffff;
    background-color: #17141d;
    border: 0;
}

.form-control:disabled,
.form-control[readonly] {
    background-color: #100e17
}

.post2Input {
    border-left: 1px solid #100e17;
}

.input-group-text input[type="checkbox"] {
    width: 25px;
    height: 25px;
}

.btn-load {
    background-color: #da1b60;
    background: linear-gradient(to right, #ff8a00, #da1b60);
    border: 0 !important;
}

.input-group,
.input-group-prepend {
    align-items: center;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    background: #17141d;
    outline: none;
    opacity: 0.7;
    border-radius: 25px;
    -webkit-transition: .2s;
    transition: opacity .2s;
}

.slider:hover {
    opacity: 1;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    background-color: #da1b60;
    background: linear-gradient(to right, #ff8a00, #da1b60);
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 100%;
    background-color: #da1b60;
    background: linear-gradient(to right, #ff8a00, #da1b60);
    cursor: pointer;
}

/* Video BackGround */
.videoBG {
    position: absolute;
    z-index: 0;
    top: 0;
    width: 100%;
    height: 100vh
}

.overlay {
    width: 100%;
    background-color: #000;
    height: 100vh;
    position: absolute;
    top: 0;
    opacity: 0.5;
}

/* Reactions Css */
.reactions {
    grid-column: 3 / 11;
    grid-row: 9 / 11;
    overflow: hidden;
    display: flex;
    margin-top: 20px;
    margin-left: 6rem;
    justify-content: space-around;
    color: #fff;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 600;
    background-color: rgba(0, 0, 0, 0.521);
    border-radius: 16px;
}

.reaction-item {
    display: grid;
    grid-template-columns: 50px 1fr;
    align-items: center;
    grid-column-gap: 10px;
}

.reaction-item img {
    width: 100%;
}

/* Comments CSS */
.comments {
    grid-column: 1 / 4;
    grid-row: 1 / 11;
    color: #fff;
    font-size: 1rem;
    padding: .5rem;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.521);
    border-radius: 16px;
}

.comment_item {
    margin-bottom: 10px;
    display: grid;
    grid-template-columns: 50px 1fr;
    grid-column-gap: 10px;
}

.avatar {
    border-radius: 100%;
    width: 50px;
    height: 50px;
    overflow: hidden;
}

.avatar img {
    width: 100%
}

.name {
    font-weight: 700;
    color: #e8004f
}

.message {
    font-weight: 600;
}

/* Video Player CSS */
.videoplayer {
    grid-column: 3 / 11;
    grid-row: 1 / 9;
    overflow: hidden;
    margin-bottom: 2.8rem;
    margin-left: 6rem;
    background-color: rgba(0, 0, 0, 0.521);
    border-radius: 16px;
}
.playerBG {
    width: 100%;
}